<template>
  <div id="app" class="ConTainer">
    <NaBar />
    <router-view />
  </div>
</template>
<script>
import NaBar from "@/components/NavBar.vue";

export default {
  name: "home",
  components: {
    NaBar,
  },
  created() {
    function getCookie(cname) {
      let name = cname + "=";
      let decodedCookie = decodeURIComponent(document.cookie);
      let ca = decodedCookie.split(";");
      for (let i = 0; i < ca.length; i++) {
        let c = ca[i];
        while (c.charAt(0) == " ") {
          c = c.substring(1);
        }
        if (c.indexOf(name) == 0) {
          return c.substring(name.length, c.length);
        }
      }
      return null;
    }
    function setCookie(cname, cvalue) {
      document.cookie = `${cname}=${cvalue};`;
    }
    if (getCookie("The_State") != null && getCookie("Token") != null) {
      if (getCookie("The_State") != "" && getCookie("Token") != "") {
        this.$store.state.Islogin = true;
        this.$store.state.The_State = getCookie("The_State");
        this.$store.state.Token = getCookie("Token");
      } else {
        this.$store.state.Islogin = false;
        this.$store.state.The_State = "User";
        this.$store.state.Token = "";
      }
    } else {
      setCookie("The_State", "");
      setCookie("Token", "");
    }
    document.title = "History Makers";
  },
};
</script>

